import { useParams } from "next/navigation";
import { useApi } from "../../context/ApiContext";
import { useToken } from "../../context/token_context";
import { useEffect, useState } from "react";

export default function RequestDetailsPage() {
  const { id } = useParams();
  const { baseUrl } = useApi();
  const { token, userData } = useToken();

  const [request, setRequest] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const role =
    userData?.["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"] || "";
  const isCustomer = role.toLowerCase() === "customer";
  const isWorker = role.toLowerCase() === "worker";

  useEffect(() => {
    if (!id || !token || (!isCustomer && !isWorker)) return;

    const fetchDetails = async () => {
      try {
        const endpoint = isCustomer
          ? `/api/Requests/customer-requests/${id}`
          : `/api/Requests/received-requests/${id}`;

        const url = `${baseUrl}${endpoint}`;
        console.log("Fetching from:", url);

        const res = await fetch(url, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!res.ok) {
          const errText = await res.text();
          throw new Error(`فشل في جلب البيانات: ${res.status} - ${errText}`);
        }

        const data = await res.json();
        setRequest(data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchDetails();
  }, [id, token, baseUrl, isCustomer, isWorker]);

  if (loading) return <p>جاري التحميل...</p>;
  if (error) return <p className="text-red-600">حدث خطأ: {error}</p>;
  if (!request) return <p>لا توجد بيانات</p>;

  return (
    <div className="p-4">
      <h2 className="text-xl font-bold mb-2">تفاصيل الطلب</h2>
      <p><strong>اسم العميل:</strong> {request.customerName}</p>
      <p><strong>الخدمة:</strong> {request.serviceName}</p>
      <p><strong>المدينة:</strong> {request.customerCity}</p>
      <p><strong>العنوان:</strong> {request.customerAddress}</p>
      <p><strong>نوع الشقة:</strong> {request.apartmentType}</p>
      <p><strong>المساحة:</strong> {request.apartmentSize}</p>
      <p><strong>نمط التشطيب:</strong> {request.preferredStyle}</p>
      <p><strong>جودة الخامات:</strong> {request.materialQuality}</p>
      <p><strong>الميزانية:</strong> من {request.minBudget} إلى {request.maxBudget}</p>
      <p><strong>السعر المقترح من العامل:</strong> {request.workerOfferedPrice}</p>
      <p><strong>السعر النهائي المتفق عليه:</strong> {request.finalAgreedPrice}</p>
      <p><strong>تاريخ الطلب:</strong> {new Date(request.requestDate).toLocaleDateString()}</p>
      <p><strong>الحالة:</strong> {request.status}</p>

      <div className="mt-4">
        <strong>صور المشروع:</strong>
        <div className="grid grid-cols-2 gap-2 mt-2">
          {request.projectImageUrls.map((url: string, index: number) => (
            <img key={index} src={url} alt={`project image ${index}`} className="w-full h-auto rounded" />
          ))}
        </div>
      </div>
    </div>
  );
}
